@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono&display=swap);
:root {
  --clr-text: #5f5f5f;
  --clr-accent: #7849ff;
  --clr-menu-text: #f7f4ff;
  --clr-bg: #dadfe7;
  --clr-fg: #fff;
  --clr-loss: #ff5353;
  --clr-selection-gain: #1be1af;
  --clr-text-secondary: #848484;
  --clr-nav-bg: #565656;
}

html {
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

body {
  font-family: 'Ubuntu Mono', monospace;
  background-color: black;
  color: #f7f4ff;
  color: var(--clr-menu-text);
  line-height: 1.6;
}

p {
    margin-top: 15px;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #1be1af;
  color: var(--clr-selection-gain);
}

h1 small {
  color: #848484;
  color: var(--clr-text-secondary);
}

.home-avatar {
  border-radius: 50%;
  margin-top: 8px;
  margin-left: 8px;
}

.main-content {
  margin: 0 auto;
  max-width: 800px;
}

.text-center {
  text-align: center;
}

.nav {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

.nav li {
  float: left;
}

.brand {
  font-weight: 500;
}
.nav li a.active {
  /* background-color: #4caf50; */
  border-bottom: 4px solid #1be1af;
  border-bottom: 4px solid var(--clr-selection-gain);
  color: #1be1af;
  color: var(--clr-selection-gain);
}

.nav li a,
.nav li div {
  display: inline-block;
  color: white;
  text-align: center;
  padding: 13px 16px;
  text-decoration: none;
}

